<template>
  <div style="margin:20px">
    <Form ref="formValidate" :model="article" :rules="ruleValidate" label-position="left" :label-width="120">
      <FormItem label="标题名称" prop="title">
        <Input v-model="article.title" placeholder="请输入标题名称！" size="large"></Input>
      </FormItem>
      <FormItem label="标题图片" prop="picture">
          <single-uploader :image='article.picture' @change='handleImgChange'></single-uploader>
      </FormItem>
      <FormItem label="内容分类" prop="typeid">
        <Select v-model="article.typeid" placeholder="请选择内容所属分类" size="large" transfer>
          <Option v-for="item in articletype" :value="item.id" :key="item.id">{{item.name}}</Option>
        </Select>
      </FormItem>
       <FormItem label="详情页跳转" prop="linktype">
            <Select placeholder='请选择跳转' v-model="article.linktype">
              <Option v-for="v,k in linkobj" :value='k' :key="k">{{v}}</Option>
            </Select>
          </FormItem>
      <FormItem label="简要说明" prop="remark">
        <Input v-model="article.remark" type="textarea" :autosize="{minRows: 2,maxRows: 5}" placeholder="请输入简要说明!" size="large"></Input>
      </FormItem>
      <FormItem label="内容介绍" prop="content">
        <!-- <vue-html5-editor :content="article.content" :height="200"  @change="updateContent"></vue-html5-editor> -->
        <vue-editor :content='article.content' @change="updateContent"></vue-editor>
      </FormItem>
      <FormItem>
        <Button type="primary" :loading="loading" @click="handleSubmit('formValidate')" size="large" style="width:120px;">
          <span v-if="!loading">提交</span>
          <span v-else>正在保存..</span>
        </Button>
        <Button type="ghost" @click="handleReset('formValidate')" size="large" style="margin-left: 8px;width:80">取消</Button>
      </FormItem>
    </Form>
  </div>
</template>
<script>
import VueEditor from '../../editor/vue2editor.vue'
export default {
  data() {
    return {
      loading: false,
      articletype: [],
      article: {
        id: '',
        title: '',
        typeid: '',
        remark: '',
        content: '',
        picture: '',
      },
       linkobj: {
        "0": "首页",
        "1": "会员介绍",
        "2": "维修介绍",
        "3": "推广介绍",
       },
      ruleValidate: {
        title: [
          { required: true, message: '标题名称不能为空！', trigger: 'blur' }
        ],
        // typeid: [
        //   { type: 'number', required: true, message: '请选择内容所属分类', trigger: 'change' }
        // ],
        remark: [
          { required: true, message: '请输入简要说明', trigger: 'blur' }
        ],
        content: [
          { required: true, message: '请输入内容介绍', trigger: 'blur' },
          { type: 'string', min: 20, message: '请输入大于20个字符！', trigger: 'blur' }
        ],
        picture: [
          { required: true, message: '请选择标题图片', trigger: 'blur' },
        ]
      }
    }
  },
  components: {
    'vue-editor': VueEditor
  },
  mounted() {
    this.$sitemap.prvname = "活动管理";
    this.$sitemap.currname = '编辑活动';
    this.getArticleTypeList();
    this.getArticleDetail();
  },
  methods: {
    handleSubmit(name) {
      var thatform = this.$refs[name];
      this.$refs[name].validate((valid) => {
        if (valid) {
          this.loading = true;
          this.editArticle(thatform);
        } else {
          this.$Message.error('还没有完整的信息输入！');
        }
      })
    },
    handleReset(name) {
      this.$Confirm('所做的修改没有保存，请确认取消', '提示').then(res => {
        this.$router.push('/article/list')
      }).catch(erro => {

      })
    },
    getArticleTypeList: function() {
      var that = this;
      this.$http.post("articletype/list", {}).then(function(res) {
        that.articletype = res.data.data;
        that.$Loading.finish();
      });
    },
    getArticleDetail: function() {
      var params = { artid: this.$route.query.id };
      var that = this;
      this.$http.post("article/get", params).then(function(res) {
        if (res.data.result == 1) {
            that.article=res.data.data
          // Object.keys(that.article).forEach(it => {
          //   that.article[it] = res.data.data[it]
          // })
        }
      });
    },
    editArticle(thatform) {
      var that = this;
      this.$http.post(this.$api.article_edit, Object.assign({},this.article,{signescape:['content']})).then(function(res) {
        if (res.data.result == 1) {
          that.$router.push('/article/list')
          that.$Message.success('内容修改成功!');
        } else {
          that.$Message.error(res.data.msg);
        }
      }).catch(erro => {
        that.$Message.error('修改失败' + erro);
      }).then(() => {
        that.loading = false;
      });
    },
    updateContent(content) {
      this.article.content = content;
    },
    handleImgChange(img) {
      this.article.picture = img.url
    },
  }
}

</script>



